'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { collection, query, where, orderBy, onSnapshot, doc, getDoc, DocumentData, Timestamp } from 'firebase/firestore';
import { db, auth } from '@/firebase/clientApp';
import { onAuthStateChanged } from 'firebase/auth';
import { format, isToday, isYesterday } from 'date-fns';
import Link from 'next/link';
import styles from '@/styles/messages.module.css';

interface Conversation {
  id: string;
  members: string[];
  memberNames?: Record<string, string>;
  memberPhotos?: Record<string, string>;
  lastMessage: string;
  updatedAt: Timestamp;
  unreadCount?: number;
  lastMessageSenderId?: string; // Track who sent the last message
}

export default function MessagesPage() {
  const [conversations, setConversations] = useState<Conversation[]>([]);
  const [loading, setLoading] = useState(true);
  const [currentUser, setCurrentUser] = useState<any>(null);
  const router = useRouter();

  // Listen to auth state changes
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (!user) {
        // Redirect to login if not authenticated
        router.push('/login?redirect=/messages');
        return;
      }
      
      setCurrentUser(user);
    });

    return () => unsubscribe();
  }, [router]);

  // Fetch conversations when user is authenticated
  useEffect(() => {
    if (!currentUser) return;
    
    // Create query for conversations where the current user is a member
    const conversationsRef = collection(db, 'conversations');
    const q = query(
      conversationsRef,
      where('members', 'array-contains', currentUser.uid),
      orderBy('updatedAt', 'desc')
    );
    
    // Set up real-time listener
    const unsubscribe = onSnapshot(q, async (snapshot) => {
      const conversationsData: Conversation[] = [];
      
      const userDetailsPromises = [];
      
      for (const docSnapshot of snapshot.docs) {
        const data = docSnapshot.data() as Conversation;
        
        // Get other member details
        const memberNames: Record<string, string> = {};
        const memberPhotos: Record<string, string> = {};
        
        // Get user details for each member
        for (const memberId of data.members) {
          if (memberId !== currentUser.uid) {
            // Store the promise for later resolution
            userDetailsPromises.push(
              getDoc(doc(db, 'users', memberId))
                .then(userSnapshot => {
                  if (userSnapshot.exists()) {
                    const userData = userSnapshot.data() as { name?: string; photoURL?: string };
                    memberNames[memberId] = userData.name || 'Unknown User';
                    memberPhotos[memberId] = userData.photoURL || '';
                  } else {
                    memberNames[memberId] = 'Unknown User';
                  }
                })
                .catch(error => {
                  console.error('Error fetching user details:', error);
                  memberNames[memberId] = 'Unknown User';
                })
            );
          }
        }
        
        // Add conversation with member details
        conversationsData.push({
          id: docSnapshot.id,
          members: data.members,
          lastMessage: data.lastMessage,
          updatedAt: data.updatedAt,
          unreadCount: data.unreadCount || 0,
          lastMessageSenderId: data.lastMessageSenderId,
          memberNames,
          memberPhotos
        });
      }
      
      // Wait for all user detail promises to resolve
      await Promise.all(userDetailsPromises);
      
      setConversations(conversationsData);
      setLoading(false);
    }, error => {
      console.error("Error fetching conversations:", error);
      setLoading(false);
    });
    
    return () => unsubscribe();
  }, [currentUser]);

  // Get the other member's name from the conversation
  const getOtherMemberName = (conversation: Conversation): string => {
    if (!currentUser || !conversation.memberNames) return 'Loading...';
    
    const otherMemberId = conversation.members.find(id => id !== currentUser.uid);
    return otherMemberId && conversation.memberNames[otherMemberId] 
      ? conversation.memberNames[otherMemberId] 
      : 'Unknown User';
  };
  
  // Get the other member's photo from the conversation
  const getOtherMemberPhoto = (conversation: Conversation): string => {
    if (!currentUser || !conversation.memberPhotos) return '';
    
    const otherMemberId = conversation.members.find(id => id !== currentUser.uid);
    return otherMemberId && conversation.memberPhotos[otherMemberId] 
      ? conversation.memberPhotos[otherMemberId] 
      : '';
  };
  
  // Format the timestamp with smart display
  const formatTimestamp = (timestamp: Timestamp): string => {
    if (!timestamp) return '';
    
    try {
      const date = timestamp.toDate();
      
      // If today, show time only
      if (isToday(date)) {
        return format(date, 'h:mm a');
      }
      
      // If yesterday, show "Yesterday"
      if (isYesterday(date)) {
        return 'Yesterday';
      }
      
      // Otherwise show date
      return format(date, 'MMM d');
    } catch (error) {
      return '';
    }
  };
  
  // Get user initials for avatar
  const getInitials = (name: string): string => {
    if (!name) return '';
    
    return name
      .split(' ')
      .map(part => part.charAt(0))
      .join('')
      .toUpperCase()
      .substring(0, 2);
  };
  
  // Check if message is unread for current user
  const isUnreadMessage = (conversation: Conversation): boolean => {
    return Boolean(
      conversation.unreadCount && 
      conversation.unreadCount > 0 && 
      conversation.lastMessageSenderId !== currentUser?.uid
    );
  };

  // Truncate message preview to reasonable length
  const truncateMessage = (message: string): string => {
    return message.length > 50 ? `${message.substring(0, 50)}...` : message;
  };

  if (loading) {
    return (
      <div className={styles.loadingContainer}>
        <div className={styles.loadingSpinner}></div>
        <p>Loading conversations...</p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.messagesHeader}>
        <h1>Messages</h1>
        <button 
          className={styles.newConversationButton}
          onClick={() => router.push('/messages/new')}
        >
          New Message
        </button>
      </div>
      
      <div className={styles.conversationsList}>
        {conversations.length === 0 ? (
          <div className={styles.emptyState}>
            <p>You don't have any conversations yet.</p>
            <p>Visit an author's profile to start a conversation.</p>
          </div>
        ) : (
          conversations.map((conversation) => (
            <Link 
              href={`/messages/${conversation.id}`}
              key={conversation.id} 
              className={`${styles.conversationItem} ${isUnreadMessage(conversation) ? styles.unreadConversation : ''}`}
            >
              <div className={styles.avatarContainer}>
                {getOtherMemberPhoto(conversation) ? (
                  <img 
                    src={getOtherMemberPhoto(conversation)} 
                    alt={getOtherMemberName(conversation)} 
                    className={styles.avatar}
                  />
                ) : (
                  <div 
                    className={styles.avatarPlaceholder}
                    style={{ 
                      background: `hsl(${getOtherMemberName(conversation).charCodeAt(0) * 10 % 360}, 70%, 65%)` 
                    }}
                  >
                    {getInitials(getOtherMemberName(conversation))}
                  </div>
                )}
                {isUnreadMessage(conversation) && (
                  <span className={styles.unreadBadge}>
                    {conversation.unreadCount! > 9 ? '9+' : conversation.unreadCount}
                  </span>
                )}
              </div>
              
              <div className={styles.conversationDetails}>
                <div className={styles.conversationHeader}>
                  <h3 className={`${styles.conversationName} ${isUnreadMessage(conversation) ? styles.unreadName : ''}`}>
                    {getOtherMemberName(conversation)}
                  </h3>
                  <span className={styles.conversationTime}>
                    {formatTimestamp(conversation.updatedAt)}
                  </span>
                </div>
                <p className={`${styles.conversationPreview} ${isUnreadMessage(conversation) ? styles.unreadPreview : ''}`}>
                  {conversation.lastMessageSenderId === currentUser?.uid && (
                    <span className={styles.sentMessagePrefix}>You: </span>
                  )}
                  {truncateMessage(conversation.lastMessage)}
                </p>
              </div>
            </Link>
          ))
        )}
      </div>
    </div>
  );
} 